import rRadio from '@/components/common/RHX/rRadio/rRadio';
import rPicker from '@/components/common/RHX/rPicker/rPicker';
import fixedBgBtn from '@/components/common/button/fixedBgBtn.vue';
export default {
	components: {
		rRadio,
		rPicker,
		fixedBgBtn
	},
	props: [
		'leftText',
		'centerText',
		'rightText',
		'tips',
		'pickerTop',
		'translateY',
		'rightColor',
		'type',
		'scrollHeight',
		'scrollY',
		'dataList',
		'marginBottomShow',
		'isAnimate'
	],
	data() {
		return {
			radioValue: {}, // 单选选中的值
		};
	},
	created() {},
	methods: {
		// 添加模块
		ModuleAdd() {
			console.log('触发添加模块按钮');
			// this.$emit('tap-ReplaceIcon');
		},
		// 触发单选按钮
		tapRadio(e) {
			console.log(e, '用户选中的值');
			this.radioValue = e; // 用户选中的值
		},
		//点击确认添加
		preserve (e) {
			console.log(e, '点击确认添加蓝色按钮');
			console.log(this.radioValue, '-------------------this.radioValue-----------------')
			if (JSON.stringify(this.radioValue) === "{}") { // 空提示
				uni.showToast({
					title: this.tips,
					icon: 'none',
					duration: 500
				});
				return
			}
			this.$emit('tap-PreserveBotttomFixed');
			this.$emit('tap-Preserve', this.radioValue);
		},
		// 点击picker取消按钮
		tapPickerCancel (e) {
			console.log(e, '点击picker取消按钮');
			this.$emit('tap-Cancel');
		},
		// 点击picker确定按钮
		tapPickerPreserve (e) {
			console.log(e, '点击picker确定按钮');
			if (JSON.stringify(this.radioValue) === "{}") { // 空提示
				uni.showToast({
					title: this.tips,
					icon: 'none',
					duration: 500
				});
				return
			}
			this.$emit('tap-Preserve', this.radioValue);
		}
	}
};
